body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
	text-align: center;
}

.title {
	color: #00000094;
}

.form-style {
	padding-top: 40px;
	box-shadow: 4px 3px 10px 1px #00000042;
	display: flex;
	align-content: center;
	/* align-items: center; */
	justify-content: space-around;
	flex-direction: column;
	max-width: 500px;
	margin: 0 auto;
	padding: 20px 20px 10px 20px;
	height: 100px;
}

.submit-btn {
	height: 26px;
	border-style: none;
	color: white;
	background-color: #2a4ca2;
	box-shadow: 4px 3px 10px 1px #00000042;
}

.submit-btn:hover {
	cursor: pointer;
	background-color: rgb(58, 106, 228);
}

.submit-btn:focus {
	outline: none;
}

.submit-btn:disabled {
	background-color: grey;
	opacity: 0.3;
	box-shadow: none;
	cursor: not-allowed;
}

.message {
	margin: 0px;
	padding: 0px;
	text-align: right;
	font-size: 0.8em;
}

.video {
	width: 100%;
	max-width: 500px;
	margin: 20px 0px;
}


.display-heading{
	padding: 0px;
	margin: 0px;
	text-align: left;
	
	margin-top: 20px;
}
.DisplayVideos-main-div {
	display: flex;
	margin: auto;
	flex-direction: column;
	width: fit-content;
	justify-content: space-around;
}

.timeline-header {
	box-shadow: 0px 3px 11px 1px #0000001c;
	background-color: white;
	position: sticky;
	width: 100%;
	z-index: 1;
}

.header-div{
	padding: 10px 20px;
	max-width: 500px;
	display: flex;
	justify-content: space-between;
	margin: 0 auto;
}

.link{
	color: black;
	text-decoration:none;
}

.header-link{
	font-weight: 800;
	opacity: 0.4;
}







.btn{
	border-style: none;
    
    background: none;
    font-weight: 800;
}

.timeline-heading {
	max-width: 500px;
	text-align: left;
	margin: auto;
}

.newsItems-main-li {
	list-style: none;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	border-bottom: 1px solid;
	border-bottom: 0.5px solid #00000038;
	padding: 20px 0px;
}

.newsItems-main-li:hover {
	cursor: pointer;
}

.list-container {
	display: flex;
	flex-direction: column;
	padding: 0px;
	margin: 0px;
	
	max-width: 500px;
	margin: auto;
	margin-top: 10px;
}

.defaultP {
	margin: 0px;
	padding: 0px;
	margin-bottom: 10px;
}

.item-div-1 {
	text-align: left;
	padding-right: 20px;
}

.source {
	font-weight: 800;
}

.content {
	font-weight: 600;
}

.date {
	font-weight: 600;
	opacity: 0.6;
}

.thumbnail {
	border-radius: 5px;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	-ms-border-radius: 5px;
	-o-border-radius: 5px;
}

.time{
	text-align: left;
	font-weight: 800;
	opacity: 0.4;
	border-bottom: 0.5px solid #00000059;
	padding-bottom: 10px;
}

@media all and (max-width: 500px) {
	.thumbnail {
		width: 100px;
		height: 100px;
	}

	.defaultP {
		font-size: 0.9em;
	}
	.newsItems-main-li{
		padding: 20px;
	}

	.display-heading{
		padding-left: 20px;
	}

	.time{
		padding-left: 20px;
	}
}
